import {call as fetchMany} from 'core/ajax';

const updateSort = (parent, children) => ({
    methodname: 'block_mmquicklink_update_sort',
    args: {
        parent,
        children,
    },
})

export const updateSorting = (
    parent,
    children,
) =>  {
    const responses = fetchMany([
        updateSort(parent, children),
    ]);

    return responses[0];
}
